.card__list-item {
    text-align: justify;
    margin: 0 0 5px 0;
    padding-left: 0px;
    text-transform: none;
    font-size: 18px;
    font-weight: 400;
}


@media (max-width: 1100px) {
    .card__list-item {
        text-align: justify;
        font-size: 16px;
    }
}


@media (max-width: 550px) {
    .card__list-item {
        font-size: 13px;
        width: 90%;
    }
}