.card__list {
    margin: 0 0 20px 0;
    padding: 0 20px;
    list-style: disc;
    color: #3d3d3d;
    display: grid;
    grid-template-columns: repeat(1, auto);
    column-gap: 70px;
}

@media (max-width: 1100px) {
    .card__list {
        display: flex;
        flex-direction: column;
    }
}