.file-link {
    display: flex;
    justify-content: center;
    width: 100%;
    padding: 0;
    box-sizing: border-box;
}

.file-link__container {
    z-index: 5;
    width: 80%;
    display: flex;
    flex-direction: column;
    align-items: stretch;
    text-decoration: none;
    color: #3d3d3d;
    padding: 30px;
    border-radius: 30px;
    transition: background-color .25s linear, transform .25s linear;
    background-color: rgba(241, 240, 240, .3);
    margin-bottom: 30px;
}

.file-link__container:hover {
    color: #000;
    background-color: rgba(241, 240, 240, .8);
    transform: scale(1.02, 1.02);
}

.file-link__title {
    text-align: justify;
    font-weight: bold;
    font-size: 18px;
    margin: 0;
    padding: 0 0 20px 0;
}

.file-link__about {
    text-align: justify;
    margin: 0;
    padding: 0 0 15px 0;
    font-size: 18px;
    text-transform: initial;
}

.file-link__call {
    text-align: right;
    margin: 0;
    padding: 0;
    font-size: 16px;
    opacity: .8;
}

@media (max-width: 830px) {

    .file-link__title {
        font-size: 16px;
    }

    .file-link__about {
        font-size: 16px;
    }

    .file-link__call {
        font-size: 15px;
    }

    .file-link__container {
        width: 100%;
    }
}

@media (max-width: 700px) {

    .file-link__container {
        grid-template-rows: 1fr 1fr 0;
        width: 100%;
    }
}

@media (max-width: 550px) {

    .file-link__call {
        font-size: 12px;
    }

    .file-link__about {
        font-size: 14px;
    }

    .file-link__title {
        font-size: 14px;
        padding-bottom: 10px;
    }
}