.card_type_with-text {
    z-index: 7;
    height: 505px;
    width: 60vw;
    max-width: 600px;
    border-radius: 30px;
    overflow: hidden;
    padding: 5px 0 25px 20px;
    background-color: #f1f0f0;
    background-image: none;
    height: 520px;
}

@media (max-width: 1200px) {
    .card_type_with-text {
        width: 40vw;
    }
}

@media (max-width: 1100px) {
    .card_type_with-text {
        width: 370px;
    }
}

@media (max-width: 550px) {
    .card_type_with-text {
        height: 400px;
        width: 270px;
    }
}

@media (max-width: 950px) {
    .grid {
        grid-template-columns: repeat(auto-fit, 390px);
    }
}

@media (max-width: 550px) {
    .grid {
        margin-top: 25px;
        grid-template-columns: repeat(auto-fit, 290px);
        grid-gap: 20px;
    }
}