.manual__link {
    z-index: 5;
    width: 80%;
    display: grid;
    grid-template-columns: min-content auto;
    grid-template-areas: "img title"
        "img subtitle"
        "img text"
        "call call";
    grid-gap: 0px 40px;
    align-items: center;
    text-decoration: none;
    color: #3d3d3d;
    padding: 30px;
    border-radius: 30px;
    transition: color .25s linear;
    transition: background-color .25s linear, transform .25s linear;
    background-color: rgba(241, 240, 240, .3);
    margin-bottom: 30px;
}

.manual__call {
    text-align: right;
    margin: 20px 0 0;
    padding: 0;
    font-size: 16px;
    opacity: .8;
    grid-area: call;
}

.manual__link:hover {
    color: #000;
    background-color: rgba(241, 240, 240, .8);
    transform: scale(1.02, 1.02);
}

@media (max-width: 700px) {
    .manual__link {
        grid-template-rows: 1fr 1fr 0;
        width: 100%;
        grid-gap: 0px 20px;
    }

    .manual__call {
        text-align: center;
        font-size: 14px;
    }
}


@media (max-width: 550px) {

    .manual__link {
        padding: 10px;
        grid-gap: 0px 10px;
    }

    .manual__call {
        text-align: center;
        font-size: 12px;
    }
}