.card__title {
    grid-area: title;
    font-weight: bold;
    text-align: left;
    font-size: 15px;
    margin: 0;
    color: #3d3d3d;
}

.card__title_big {
    font-size: 17px;
    text-align: justify;
    padding-right: 10px;
    padding-bottom: 10px;
}

@media (max-width: 1100px) {
    .card__title_big {
        font-size: 15px;
    }
}

@media (max-width: 550px) {
    .card__title {
        font-size: 12px;
        width: 90%
    }
}