.text_italic {
    font-style: italic;
    padding-right: 20px;
    text-transform: uppercase;
    font-size: 15px;
    text-align: justify;
    margin: 0 0 5px 0;
    padding-left: 0px;
    font-weight: 200;
    opacity: .7;
}

.title {
    display: none;
}


@media (max-width: 1100px) {
    .text_italic {
        font-size: 12px;
    }
}

@media (max-width: 550px) {
    .text_italic {
        font-size: 11px;
        width: 90%;
    }
}