.card__text {
    height: 100%;
    width: 100%;
    border-radius: 0;
    overflow-y: scroll;
    list-style: none;
    padding: 0;
    border-bottom-style: solid;
    border-bottom-width: 0;
    display: grid;
    grid-template-columns: auto;
    gap: 40px;
    font-size: 16px;
    text-align: justify;
    text-transform: none;
}