.card__text_level_machines {
    scrollbar-color: #C63332 #E9E9E9;
    scrollbar-width: thin;
    border-bottom-color: #C63332;
}

.card__text_level_machines::-webkit-scrollbar {
    width: 15px;
}

.card__text_level_machines::-webkit-scrollbar-track {
    background: #E9E9E9;
    border-radius: 15px;
}


.card__text_level_machines::-webkit-scrollbar-thumb {
    background-color: #C63332;
    border-radius: 15px;
    padding-right: 30px;
    border: 3px solid #E9E9E9;
}